import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import MainIcon from '../images/main.jpg'
import 'font-awesome/css/font-awesome.min.css';

import Img2 from '../images/projects/1.jpg'






import Header from "./header"
import "./layout.css"
import './bootstrap.css'
import './Responsive.css'
import "./litle-style.css"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <div className="hero_area">
        <div className="hero_bg_box">
          <img src={MainIcon} alt=""></img>
        </div>
        <Header siteTitle={data.site.siteMetadata?.title || `Title`} />

        <section className="slider_section ">
          <div id="customCarousel1" className="carousel slide" data-ride="carousel">
            <div className="carousel-inner">
              <div className="carousel-item active">
                <div className="container ">
                  <div className="row">
                    <div className="col-lg-8 col-xl-6 mx-auto ">
                      <div className="iMac:mb-8 iMac:text-9xl font-serif font-bold text-white lg:text-7xl">
                        <h1 className="text">
                          Altyn Juldyz
                        </h1>
                        <p>
                          Группа компаний в Казахстане, вносящая огромный вклад в развитие разных отраслей страны
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      {/* COMFORT JAGALAU */}
      <section className="about_section layout_padding background-div">
        <div id="customCarousel1" className="carousel slide" data-ride="carousel">
          <div className="carousel-inner">
            <div className="carousel-item active">
              <div className="container ">
                <div className="row">
                  <div className="col-lg-8 col-xl-6 mx-auto ">
                    <div className="iMac:mb-8 iMac:text-9xl font-serif font-bold text-white lg:text-7xl">
                      <h3 className="2">
                        COMFORT JAGALAU
                      </h3>
                      <p>
                        Мы строим многофункциональный жилой комплекс в новом центре Алматинской области в городе Конаева. Для реализации этой цели мы собрали профессиональную команду в области дизайна, проектирования и строительства
                      </p>
                      <div className="custom-btn btn-14"> Подробнее
                      </div>
                      <div>
                        <h2 className="top">Наши преимущества</h2>
                      </div>
                      <div>
                        <img src="" alt="" ></img>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Блок Швейно-обувная фабрика */}
      <section className="about_section layout_padding background-div2">
        <h1>Швейно-обувная фабрика</h1>

      </section>

      {/* Блок ИНТЕРНЕТ МАГАЗИН */}
      <section className="about_section layout_padding background-div3">
        <h1>ИНТЕРНЕТ МАГАЗИН</h1>
      </section>

      {/* Слайдер должен быть  */}
      <section className="about_section layout_padding background-div4">
        <section id="slider_bl">
          <div className="wrapper">
            <input type="radio" name="slider" id="slide1" checked />
            <input type="radio" name="slider" id="slide2" />
            <input type="radio" name="slider" id="slide3" />
            <input type="radio" name="slider" id="slide4" />
            <div className="slider-wrapper">
              <div className="inner">
                <article>
                  <div className="info top-left"></div>
                  <img src={Img2} alt="Slide 1" />
                </article>  
                <article>
                  <div className="info bottom-right"></div>
                  <img src="https://obninsksite.ru/assets/theme/images/blog/slider/2.jpg" alt="Slide 2" />
                </article>
                <article>
                  <div className="info bottom-left"></div>
                  <img src="https://obninsksite.ru/assets/theme/images/blog/slider/3.jpg" alt="Slide 3" />
                </article>
                <article>
                  <div className="info top-right"></div>
                  <img src="https://obninsksite.ru/assets/theme/images/blog/slider/4.jpg" alt="Slide 4" />
                </article>
              </div>
            </div>
            <div className="slider-prev-next-control">
              <label htmlFor="slide1"></label>
              <label htmlFor="slide2"></label>
              <label htmlFor="slide3"></label>
              <label htmlFor="slide4"></label>
            </div>
            <div className="slider-dot-control">
              <label htmlFor="slide2"></label>
              <label htmlFor="slide1"></label>
              <label htmlFor="slide3"></label>
              <label htmlFor="slide4"></label>
            </div>
          </div>
        </section>
      </section>  

      {/* О компании */}
      <section className="about_section layout_padding background-div5">
        <h1>О компании</h1>
      </section>

      {/* Наши ценности */}
      <section className="about_section layout_padding background-div6">
        <h1>Наши ценности</h1>
      </section>


      {/* Карьера в Altyn Juldyz Будь частью большой команды! */}
      <section className="about_section layout_padding background-div7">
        <h1>Карьера в Altyn Juldyz Будь частью большой команды!</h1>
      </section>
    </>
  )
}

export default Layout
