import * as React from "react"
import { Link } from "gatsby"
import MainIcon from '../images/logo-light.svg'

const Menu = [
  {
    text: "Компания",
    url: "miras",
    badge: false,
    description: "A simple example of linking to another page within a Gatsby site",
  },
  {
    text: "Проекты",
    url: "page-2",
    badge: false,
    description: "A simple example of linking to another page within a Gatsby site",
  },
  {
    text: "Магазин",
    url: "miras3",
    badge: false,
    description: "A simple example of linking to another page within a Gatsby site",
  },
  {
    text: "Карьера",
    url: "miras4",
    badge: false,
    description: "A simple example of linking to another page within a Gatsby site",
  },
  {
    text: "Контакты",
    url: "miras5",
    badge: false,
    description: "A simple example of linking to another page within a Gatsby site",
  },
]

const Header = () => (
  <>
    <header className="header_section">
      <div className="container">
        <nav className="navbar navbar-expand-lg custom_nav-container ">
          <Link to="/" className="navbar-brand">
            <img src={MainIcon} alt="" />
          </Link>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className=""> </span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav">
            <li className="nav-item" >
                <Link className="nav-link" to="/miras">Компания</Link>
              </li>
              <li className="nav-item" >
                <Link className="nav-link" to="/miras">Проекты </Link>
              </li>
              <li className="nav-item" >
                <Link className="nav-link" to="/miras">Магазин</Link>
              </li>
              <li className="nav-item" >
                <Link className="nav-link" to="/miras">Карьера</Link>
              </li>
              <li className="nav-item" >
                <Link className="nav-link" to="/miras">Контакты</Link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    </header>
  </>
)

export default Header
